	module.exports = {"data":{"markdownRemark":{"html":"<p>   The desolation of Smaug during the 2700s destroyed much the farming\nindustry that surrounded the towns of Dale and Esgaroth. It was not until the\nvirtual peace after the \"War of the Rings\" that farming activities could be\nresumed. One of the first pioneers and arguably the most successful was Barley\nof Dale. His farmstead occupies a vast area of land to the west of Dale\ngrowing wheat, corn, maize and barley. He also maintains a large herd of\ncattle and substantial piggery. His great wealth has attracted bandits, most\nnotably the young and daring bandit \"Taben the Swift\".  The success of Taben\nhas lead Barley to take great precaution  to maintain the outer perimeter of\nhis farmstead. Farmer Barley's fierce nature and hard treatment of his workers\ncontributes to this farmsteads continual expansion despite the bandit raids.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"Farmers of the Laketown Plains"}}},"pathContext":{"path":"/books/farmersofthelaketownplains"}}

